<template>
    <div class="stock">
        <div class="wrapper">
            <span class="block-head">Розыгрыши и акции</span>

            <div class="filters" ref="filters">
                <div class="act-line" v-if="isInited" :style="styles"></div>
                <a href="#" :class="{'act': filterBy === 'active'}" @click.prevent="filterBy = 'active'" class="type_active">
                    <span class="text">Активные</span>
                </a>
                <a href="#" :class="{'has-num': inUse, 'act': filterBy === 'use'}" @click.prevent="filterBy = 'use'" class="type_use">
                    <span class="text">Вы участвуете</span>
                    <span v-if="inUse" class="num">{{ inUse }}</span>
                </a>
                <a href="#" :class="{'act': filterBy === 'ended'}" @click.prevent="filterBy = 'ended'" class="type_ended">
                    <span class="text">Закончившиеся</span>
                </a>
            </div>

            <div class="stock-main">

                <div v-for="(item, index) in items" :key="index" class="item">
                    <div class="img" @click="open = item">
                        <img :src="item.image" />
                        <span v-if="item.type === 'new'" class="status">Новый розыгрыш</span>
                        <span v-if="item.type === 'use'" class="status status-y">Вы участвуете</span>
                        <span v-if="item.type === 'winner'" class="status status-w">Вы победили</span>
                        <div class="hover">
                            <a href="#" class="red-btn" @click.prevent="open = item">Подробнее</a>
                        </div>
                    </div>
                    <div class="info">
                        <span v-if="item.startDate && item.endDate" class="date">
                            С {{ item.startDate | moment('D MMMM') }} по {{ item.endDate | moment('D MMMM') }}
                        </span>
                        <span class="name">{{ item.name }}</span>
                        <span class="desc">{{ item.short_description }}</span>
                    </div>
                </div>

                <div class="empty-list-message" v-if="items.length < 1 && filterBy === 'active'">

                    Нет активных акций

                </div>

                <div class="empty-list-message" v-if="items.length < 1 && filterBy === 'use'">

                    Нет акций, в которых вы участвуете

                </div>

                <div class="empty-list-message" v-if="items.length < 1 && filterBy === 'ended'">

                    Нет прошедших акций

                </div>

            </div>
        </div>

        <div v-if="open" class="popup">
            <a href="#" class="close" @click.prevent="open = null"></a>
            <div class="popup-inner popup-inner-stock">
                <div class="img">
                    <img :src="open.image">
                    <span v-if="open.type === 'new'" class="status">Новый розыгрыш</span>
                    <span v-if="open.type === 'use'" class="status status-y">Вы участвуете</span>
                    <span v-if="open.type === 'winner'" class="status status-w">Вы победили</span>
                </div>
                <div class="info">
                    <span v-if="open.startDate && open.endDate" class="date">
                        С {{ open.startDate | moment('D MMMM') }} по {{ open.endDate | moment('D MMMM') }}
                    </span>
                    <span class="name">{{ open.name }}</span>
                    <span class="desc" v-html="open.description"></span>
                </div>
                <a v-if="!open.hideBtn && (open.type !== 'winner' && open.type !== 'use' && open.type !== 'ended')" href="#" class="red-btn" @click.prevent="onJoin(open)">{{ loading ? 'Загрузка...' : 'Принять участие' }}</a>
                <a v-if="!open.hideBtn && open.type === 'use'" href="#" class="red-btn" @click.prevent="onDisconnect(open)">{{ loading ? 'Загрузка...' : 'Не участвовать' }}</a>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters } from "vuex";
const REQUEST_TAGS_TIMEOUT = 2000;
export default {
  name: "AppShares",
  data() {
    return {
      isMounted: false,
      loading: false,
      open: null,
      filterBy: "active"
    };
  },
  watch: {
    open(value) {
      let className = "lock-scroll";
      if (value) {
        document.documentElement.classList.add(className);
      } else {
        document.documentElement.classList.remove(className);
      }
    }
  },
  computed: {
    ...mapGetters(["currentTags", "remoteConfig", "isInited"]),
    inUse() {
      if (!this.remoteConfig || !this.remoteConfig.shares) return;
      let now = this.$moment();
      return (
        this.currentTags &&
        this.currentTags.length && 
        this.remoteConfig &&
        this.remoteConfig.shares.filter(item => {
          let isBefore = this.$moment(item.endDate).isBefore(now);
          return this.$options.filters["tagExist"](item.tag) && !isBefore;
        }).length
      );
    },
    items() {
      let items = [];
      if (this.remoteConfig && this.remoteConfig.shares) {
        let now = this.$moment();
        items = this.remoteConfig.shares
          .filter(item => {
            let tagExist = item.tag
              ? this.$options.filters["tagExist"](item.tag)
              : false;
            let isBefore = this.$moment(item.endDate).isBefore(now);
            if (this.filterBy === "use") {
              return tagExist && !isBefore;
            } else if (this.filterBy === "active") {
              return !tagExist && !isBefore;
            } else if (this.filterBy === "ended") {
              return isBefore;
            }
          })
          .map(item => {
            let tagExist = item.tag
              ? this.$options.filters["tagExist"](item.tag)
              : false;
            let isNew = this.$moment(
              this.$moment().subtract(7, "days")
            ).isBefore(this.$moment(item.startDate));
            let isWinner = item.winnerTag
              ? this.$options.filters["tagExist"](item.winnerTag)
              : false;
            let isBefore = this.$moment(item.endDate).isBefore(now);
            if (isNew) {
              item.type = "new";
            }
            if (tagExist) {
              item.type = "use";
            } else {
              item.type = "";
            }
            if (isWinner) {
              item.type = "winner";
            }
            if (isBefore) {
              item.type = "ended";
            }
            return item;
          });
      }
      return items;
    },
    styles() {
      if (!this.isMounted) return;
      let styles = {
        width: 0,
        left: 0
      };
      let filters = this.$refs["filters"];
      let filtersRect = filters.getBoundingClientRect();
      let filterBy = this.filterBy;
      if (filterBy === "active") {
        let rect = filters
          .querySelector(".type_active")
          .getBoundingClientRect();
        styles.width = rect.width;
        styles.left = rect.left - filtersRect.left;
      } else if (filterBy === "use") {
        let rect = filters.querySelector(".type_use").getBoundingClientRect();
        styles.width = rect.width;
        styles.left = rect.left - filtersRect.left;
      } else if (filterBy === "ended") {
        let rect = filters.querySelector(".type_ended").getBoundingClientRect();
        styles.width = rect.width;
        styles.left = rect.left - filtersRect.left;
      }
      styles.width += "px";
      styles.left += "px";
      return styles;
    }
  },
  methods: {
    onJoin(item) {
      if (this.loading) return;
      let params = {
        add: [item.tag]
      };
      let onFinish = () => {
        this.loading = false;
        this.open = false;
        this.filterBy = "use";
      };
      this.loading = true;
      this.$store.dispatch("changeTags", params).then(() => {
        setTimeout(() => {
          this.$store.dispatch("fetchTags").then(onFinish, onFinish);
        }, REQUEST_TAGS_TIMEOUT);
      });
    },
    onDisconnect(item) {
      if (this.loading) return;
      let params = {
        remove: [item.tag]
      };
      let onFinish = () => {
        this.loading = false;
        this.open = false;
        this.filterBy = "active";
      };
      this.loading = true;
      this.$store.dispatch("changeTags", params).then(() => {
        setTimeout(() => {
          this.$store.dispatch("fetchTags").then(onFinish, onFinish);
        }, REQUEST_TAGS_TIMEOUT);
      });
    }
  },
  mounted() {
    this.isMounted = true;
  }
};
</script>
