<script>
import { mapGetters } from 'vuex'
export default {
  name: 'AppMain',
  data () {
    return {
      isOpenPopup: false
    }
  },
  computed: {
    ...mapGetters(['currentUser', 'currentPoints', 'nextGift'])
  },
  watch: {
    isOpenPopup(value) {
      let className = 'lock-scroll';
      if(value) {
        document.documentElement.classList.add(className);
      } else {
        document.documentElement.classList.remove(className);
      }
    }
  },
  methods: {
    onChangeAvatar (e) {
      let file = e.target && e.target.files && e.target.files[0]
      this.$store.dispatch('changeAvatar', file).then(() => {
        this.$store.dispatch('fetchUser')
      })
    },
    scrollToGift (id) {
      this.$eventBus.$emit('giftOpen', id)
    },
    openProfile () {
      this.$eventBus.$emit('popup', 'profile')
    },
    openHistory () {
      this.$eventBus.$emit('popup', 'history')
    }
  }
}
