<template>
  <div id="app" v-show="isInited">
    <app-header ref="header" :items="menu" @onSelect="onMenuClick" />
    <app-main ref="profile" />
    <app-quests ref="actions" />
    <app-gifts ref="gifts" />
    <app-popup ref="popup" />
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import AppHeader from '@/components/AppHeader'
import AppMain from '@/components/AppMain'
import AppGifts from '@/components/AppGifts'
import AppQuests from '@/components/AppQuests'
import AppBadges from '@/components/AppBadges'
import AppShares from '@/components/AppShares'
import AppPopup from '@/components/AppPopup'

export default {
  data () {
    return {
      isMounted: false
    }
  },
  props: {
    loading: Boolean,
    default: false
  },
  components: {
    AppHeader,
    AppMain,
    AppGifts,
    AppQuests,
    AppBadges,
    AppShares,
    AppPopup
  },
  computed: {
    ...mapGetters(['remoteConfig', 'isInited']),
    // Menu items
    menu () {
      if (!this.isMounted) return
      return [
        {
          alias: 'profile',
          name: 'Profile',
          ref: this.$refs['profile']
        },
        {
          alias: 'actions',
          name: 'Actions for points',
          ref: this.$refs['actions']
        },
        {
          alias: 'gifts',
          name: 'Rewards',
          ref: this.$refs['gifts']
        }
      ]
    }
  },
  methods: {
    onMenuClick (item) {
      if (item.ref) {
        let rect = item.ref.$el && item.ref.$el.getBoundingClientRect()
        let headerHeight = this.$refs['header'].$el.querySelector('.header')
          .clientHeight
        let offsetY = rect.top + window.pageYOffset - headerHeight
        if (offsetY !== 0) {
          window.scrollTo(rect.left, offsetY)
        }
      }
    }
  },
  mounted () {
    this.isMounted = true
  }
}
</script>

