<template>
    <div class="right">
        <span class="popup-head">
            История
        </span>
        <span v-if="currentHistory && !currentHistory.length" class="popup-desc">
            Тут будет история ваших действий
        </span>
        <div v-if="currentHistory && currentHistory.length" class="history">
            <table>
                <tr>
                    <th>Дата</th>
                    <th>Событие</th>
                    <th>Баллы</th>
                </tr>

                <tr v-for="(item, index) in currentHistory" :key="index">
                    <td>{{ item.action_date | moment('DD MMM') }}</td>
                    <td>
                        <span class="name">{{ historyName(item) }}</span>
                        <span v-if="historyName(item) !== item.name" class="dop">{{ item.name }}</span>
                    </td>
                    <td>
                        <span class="ball">{{ item.points_delta > 0 ? '+' + item.points_delta : item.points_delta }}</span>
                    </td>
                </tr>

            </table>
        </div>
    </div>
</template>

<script>
import { mapGetters } from "vuex";
const DEFAULT_HISTORY_ITEMS = {
  purchase: "Покупка",
  gift_purchase: "Подарок",
  badge: "Бейджик",
  registration: "Регистрация",
  referral: "Регистрация друга",
  referred: "Регистрация по приглашению",
  referred_purchase: "Покупка приглашенного пользователя",
  promocode: "За ввод промокода",
  enter_group: "Вступление в группу ",
  share_purchase: "Рассказ о покупке в ",
  social_share: "Рассказ в ",
  share_badge: "Рассказ о бейджике в ",
  earn_badge: "Получен бейджик "
};
export default {
  name: "AppPopupHistory",
  computed: {
    ...mapGetters(["currentHistory", "remoteConfig"])
  },
  methods: {
    historyName(action) {
      if (!action) return "";
      let historyItems =
        (this.remoteConfig && this.remoteConfig.historyItems) ||
        DEFAULT_HISTORY_ITEMS;
      switch (action.action) {
        case "custom_action":
          return action.name || "";
        case "event":
          return action.name || "";
        case "extra":
          return action.name || "";
        case "sharing":
          switch (action.social_action) {
            case "like":
              return historyItems.enter_group + action.social_type;
            case "purchase":
              return historyItems.share_purchase + action.social_type;
            case "partner_page":
              return historyItems.social_share + action.social_type;
            case "badge":
              return historyItems.share_badge + action.social_type;
          }
      }
      return historyItems[action.action] || "";
    }
  }
};
</script>
