<template>
    <div class="tasks">
        <div class="wrapper">
            <div v-for="(quest, index) in quests" :key="index" :class="{'has-share': quest.vk || quest.fb}" class="task-item">

                <div v-if="quest.vk || quest.fb" key="social_quest" @click="openQuest=true">
                    <div class="img">
                        <img :src="quest.data.pic">
                    </div>
                    <span class="head">{{ quest.data.name }}</span>
                    <span class="ball">
                    +{{ quest.data.points }} {{ quest.data.points | pluralize('балл,балла,баллов') }}
                </span>
                </div>

                <a v-else-if="quest._actionId && (!quest.vk && !quest.fb)" key="custom_quest" v-system-action="quest"  @click="openQuest=true">
                    <div class="img">
                        <img :src="quest.data.pic">
                    </div>
                    <span class="head">{{ quest.data.name }}</span>
                    <span class="ball">
                    +{{ quest.data.points }} {{ quest.data.points | pluralize('балл,балла,баллов') }}
                </span>
                </a>

                <div v-else-if="!quest._actionId" key="custom_quest" @click="onOpenCustom(quest); openQuest=true" >
                    <div class="img">
                        <img :src="quest.data.pic">
                    </div>
                    <span class="head">{{ quest.data.name }}</span>
                    <span class="ball">
                    +{{ quest.data.points }} {{ quest.data.points | pluralize('балл,балла,баллов') }}
                </span>
                </div>

            </div>

        </div>


        <div v-if="openQuest" class="popup">
            <div class="popup-inner popup-inner-gift popup-inner-gift-quest">
                <a href="#" class="close" @click.prevent="openQuest=false"></a>
                <div class="info">
                    <span class="name">Speak at the external event</span>
                    <span class="desc">Perform a speech, lecture or a workshop on one of the industry recognized events and receive recognition for each of your public talks.</span>
                    <a @click.prevent href="#" class="red-btn">500 points</a>
                </div>
                <div class="img">
                    <img src="../assets/img/quest1.png">
                </div>
                <div class="bottom">
                    To receive the award for successful execution of this action please submit your recognition request to your manager or to hr@placeholder.com.
                </div>
            </div>
        </div>

        <!--<div v-if="open" class="popup">-->
            <!--<div v-if="open._actionId" key="open_social" class="popup-inner popup-inner-quest">-->
                <!--<div class="img"></div>-->
                <!--<span class="head">{{ open.data.name }}</span>-->
                <!--<span class="ball">-->
                    <!--+{{ open.data.points }} {{ open.data.points | pluralize('балл,балла,баллов') }}-->
                <!--</span>-->
