            <!--</div>-->

            <!--<div v-else-if="!open._actionId" key="open_custom" class="popup-inner popup-inner-iframe">-->
                <!--<iframe class="custom-iframe" :src="open | customIframe"></iframe>-->
            <!--</div>-->

        <!--</div>-->

    </div>
</template>

<script>
import { mapGetters } from "vuex";
import SAILPLAY from "sailplay-hub";
import CONFIG from "../../config";
let DEFAULT_BUTTON = {
  "font-size": "15px",
  "font-weight": "bold",
  display: "inline-block",
  color: "#ffffff",
  "line-height": "40px",
  "background-color": "#cd3c3c",
  padding: "0 30px",
  "text-decoration": "none",
  "font-family": "Arial",
  cursor: "pointer"
};
const DEFAULT_STYLES = {
  fb_share_btn: DEFAULT_BUTTON,
  vk_share_btn: DEFAULT_BUTTON
};
const DELAY_AFTER_SUCCESS = 2000;
const stringifyCss = (prefix, obj) => {
  let css_string = "";
  let media_queries = "";

  for (let selector in obj) {
    if (obj.hasOwnProperty(selector)) {
      if (selector[0] == "@" && selector.split("|").length > 1) {
        var media = selector.split("|")[0].trim();
        var cls = selector.split("|")[1].trim();
        media_queries += media + "{ " + prefix + " ." + cls + "{ ";
      } else css_string += prefix + " ." + selector + "{ ";

      let selector_styles = obj[selector];

      for (let prop in selector_styles) {
        if (selector_styles.hasOwnProperty(prop)) {
          if (selector[0] == "@") {
            media_queries +=
              prop + ":" + selector_styles[prop] + " !important;";
          } else {
            css_string += prop + ":" + selector_styles[prop] + " !important;";
          }
        }
      }

      if (selector[0] == "@") media_queries += " } }";
      else css_string += " }";
    }
  }

  css_string += media_queries;
  return css_string;
};
export default {
  name: "AppQuests",
  data() {
    return {
      isMounted: false,
      open: null,
      openQuest: false
    };
  },
  directives: {
    systemAction: {
      bind: (el, binding) => {
        let value = binding.value;
        el.setAttribute(
          "data-styles",
          stringifyCss("", value.data.styles || DEFAULT_STYLES)
        );
        el.setAttribute("data-text", value.data.button_text);
        SAILPLAY.actions && value && SAILPLAY.actions.parse(el, value);
      }
    }
  },
  watch: {
    open(value) {
      let className = "lock-scroll";
      if (value) {
        document.documentElement.classList.add(className);
      } else {
        document.documentElement.classList.remove(className);
      }
    }
  },
  computed: {
    ...mapGetters(["questList", "remoteConfig"]),
    quests() {
      if (!this.isMounted || !this.remoteConfig || !this.questList) return;
      let data = this.remoteConfig.quests;

      let socialsQuests = this.questList.filter(item => item.socialType);
      let quests = socialsQuests.reduce((total, current) => {
        let item = total.find(item => item.action === current.action);
        if (!item) {
          let item = {
            _actionId: current._actionId,
            action: current.action,
            [current.socialType]: current,
            data: data.social[current.action]
          };
          total = [...total, item];
        } else {
          item[current.socialType] = current;
        }
        return total;
      }, []);

      let customQuests = this.questList.filter(item => !item._actionId);
      quests = [
        ...quests,
        ...customQuests.map(item => {
          item.data = data.custom[item.type];
          return item;
        })
      ];

      let otherQuests = this.questList.filter(
        item => !item.socialType && item._actionId
      );
      quests = [
        ...quests,
        ...otherQuests.map(item => {
          item.data = data.system[item.type];
          return item;
        })
      ];
      return quests;
    }
  },
  filters: {
    customIframe(action) {
      let config = SAILPLAY.config();
      return (
        (CONFIG &&
          CONFIG.domain +
            config.urls.actions.custom.render.replace(":action_id", action.id) +
            "?auth_hash=" +
            config.auth_hash +
            "&lang=" +
            config.lang +
            "&config=" +
            (CONFIG.remote_config_name || "default")) ||
        ""
      );
    }
  },
  methods: {
    onOpenSocial(quest, social) {
      let _quest = this.questList.find(
        item => quest.action === item.action && item.socialType === social
      );
      _quest.data = quest.data;
      this.open = Object.assign({}, _quest);
    },
    onClosePopup(item) {
      this.open = false;
      if (!this.open._actionId) this.$store.dispatch("fetchQuests");
    },
    onOpenCustom(quest) {
      this.open = Object.assign({}, quest);
    }
  },
  mounted() {
    this.isMounted = true;
    SAILPLAY.on("actions.perform.success", action => {
      setTimeout(() => {
        this.open = false;
        this.$store.dispatch("fetchQuests");
      }, DELAY_AFTER_SUCCESS);
    });
  }
};
</script>
