import Vue from 'vue'
import Vuex from 'vuex'
import Api from '@/ApiService'
import Cookies from 'js-cookie'
import CONFIG from '../config'

Vue.use(Vuex)

const DEFAULT_AUTH_COOKIE = 'auth_hash_cookie'

export default new Vuex.Store({
  state: {
    inited: false,
    user: null,
    history: null,
    stores: null,
    tags: null,
    gifts: null,
    config: null,
    badges: null,
    quests: null

  },
  getters: {
    isInited: state => state.inited,

    currentUser: state => state.user && state.user.user,
    currentPoints: state => state.user && state.user.user_points && state.user.user_points.confirmed,
    currentHistory: state => state.history,
    currentTags: state => state.tags,

    storeList: state => state.stores && state.stores.list,
    storeCategories: state => state.stores && state.stores.categories,

    currentUserStores: (state, getters) => {
      return getters.storeList && getters.currentTags && getters.storeList.filter(store => ~getters.currentTags.map(item => item.tag).indexOf(store.name))
    },
    currentUserCategories: (state, getters) => {
      return getters.storeCategories && getters.currentTags && getters.storeCategories.filter(category => ~getters.currentTags.map(item => item.tag).indexOf(category))
    },

    remoteConfig: state => state.config,

    giftList: state => state.gifts,
    nextGifts: (state, getters) => {
      return getters.currentUser && getters.giftList && getters.giftList.sort((a, b) => parseInt(a.points) - parseInt(b.points)).filter(item => item.points > getters.currentPoints)
    },
    nextGift: (state, getters) => getters.nextGifts && getters.nextGifts[0],

    badgeList: state => state.badges,
    questList: state => state.quests

  },
  mutations: {

    ready (state) {
      state.inited = true
    },
    updateUserInfo (state, user) {
      state.user = user
    },
    updateHistory (state, history) {
      state.history = history
    },
    updateTags (state, tags) {
      state.tags = tags
    },
    updateStores (state, data) {
      state.stores = data
    },
    updateGifts (state, gifts) {
      state.gifts = gifts
    },
    updateConfig (state, config) {
      state.config = config
    },
    updateBadges (state, badges) {
      state.badges = badges
    },
    updateQuests (state, quests) {
      state.quests = quests
    }

  },
  actions: {

    init () {
      return Api.init()
    },

    fetchUser ({
      commit
    }, notCommit) {
      return new Promise((resolve, reject) => {
        Api.userInfo().then(res => {
          if(!notCommit)
            commit('updateUserInfo', res)
          resolve(res)
        }, reject)
      })
    },

    fetchHistory ({
      commit
    }) {
      return new Promise((resolve, reject) => {
        Api.userHistory().then(res => {
          commit('updateHistory', res)
          resolve(res)
        }, reject)
      })
    },

    fetchStores ({
      commit
    }) {
      return new Promise((resolve, reject) => {
        fetch(CONFIG.stores_url || 'stores.json')
          .then(r => r.json(), reject)
          .then(json => {
            commit('updateStores', json)
            resolve(json)
          }, reject)
      })
    },

    fetchTags ({
      commit
    }, notCommit) {
      return new Promise((resolve, reject) => {
        Api.userTags().then(res => {
          if (!notCommit) {
            commit('updateTags', res.tags)
          }
          resolve(res)
        }, reject)
      })
    },

    fetchGifts ({
      commit
    }) {
      return new Promise((resolve, reject) => {
        Api.giftsList().then(res => {
          if (res && res.length) {
            let gifts = res.map(item => {
              try {
                item.data = JSON.parse(item.descr)
              } catch (e) {};
              return item
            })
            commit('updateGifts', gifts)
          }
          resolve(res)
        }, reject)
      })
    },

    fetchConfig ({
      commit
    }) {
      return new Promise((resolve, reject) => {
        Api.config().then(res => {
          commit('updateConfig', res.config.config)
          resolve(res)
        }, reject)
      })
    },

    fetchQuests ({
      commit
    }) {
      return new Promise((resolve, reject) => {
        Promise.all([Api.loadActions(), Api.loadCustomActions()]).then(res => {
          let system = res.find(item => !Array.isArray(item)).actions
          let custom = res.find(item => Array.isArray(item))
          let quests = [...system, ...custom]
          commit('updateQuests', quests)
          resolve(quests)
        }, reject)
      })
    },

    userUpdate ({
      commit
    }, params) {
      return Api.userUpdate(params)
    },

    changeAvatar ({
      commit
    }, file) {
      return Api.changeAvatar(file)
    },

    changeTags ({
      commit
    }, tags) {
      return new Promise((resolve, reject) => {
        if (tags.remove && tags.remove.length) {
          Api.removeTags(tags.remove).then(() => {
            if (tags.add && tags.add.length) {
              Api.addTags(tags.add).then(resolve, reject)
            } else {
              resolve()
            }
          }, reject)
        } else if (tags.add && tags.add.length) {
          Api.addTags(tags.add).then(resolve, reject)
        } else {
          resolve()
        }
      })
    },

    fetchBadges ({ commit }) {
      return new Promise((resolve, reject) => {
        Api.badgesList().then(res => {
          if (res) {
            let oneLevelBadges = res.one_level_badges.map(item => {
              try {
                item.data = JSON.parse(item.descr)
              } catch (e) {};
              return item
            })
            let multilevelBadges = res.multilevel_badges.map(level => {
              return level.map(item => {
                try {
                  item.data = JSON.parse(item.descr)
                } catch (e) {};
                return item
              })
            })
            commit('updateBadges', {
              oneLevelBadges,
              multilevelBadges
            })
          }
          resolve(res)
        })
      })
    },

    getGift ({
      commit
    }, gift) {
      return Api.getGift(gift)
    },

    checkLogin ({
      commit,
      dispatch
    }) {
      return new Promise((resolve, reject) => {
        const authHash = Cookies.get(CONFIG.auth_cookie_name || DEFAULT_AUTH_COOKIE)
        if (authHash) {
          Api.login(authHash).then((user) => {
            const userInfo = dispatch('fetchUser')
            const userHistory = dispatch('fetchHistory')
            const stores = dispatch('fetchStores')
            const tags = dispatch('fetchTags')
            const gifts = dispatch('fetchGifts')
            const config = dispatch('fetchConfig')
            const badges = dispatch('fetchBadges')
            const quests = dispatch('fetchQuests')
            const promises = [userInfo, userHistory, stores, tags, gifts, config, badges, quests]
            return Promise.all(promises).then(resolve, reject).then(() => {
              commit('ready')
            })
          }, reject)
        } else {
          reject(authHash)
        }
      })
    }

  }
})
