<template>
    <div class="popup" v-if="show">
        <a href="#" class="close" @click.prevent="onChange(false)"></a>
        <div class="popup-inner">
            <div class="left">
                <div class="left-wrap">
                    <div class="popup-menu">
                        <a href="#" :class="{'act': type === 'profile' ||  type === 'profile-edit'}" @click.prevent="onChange('profile')">Личные данные</a>
                        <a href="#" :class="{'act': type === 'channels'}" @click.prevent="onChange('channels')">Каналы коммуникации</a>
                        <a href="#" :class="{'act': type === 'favorites'}" @click.prevent="onChange('favorites')">Избранное</a>
                        <a href="#" :class="{'act': type === 'history'}" @click.prevent="onChange('history')">История</a>
                    </div>
                    <a v-if="remoteConfig && remoteConfig.logoutLink" :href="remoteConfig.logoutLink" class="exit-btn">Выйти</a>
                </div>
                <div class="banner">
                  <a :href="remoteConfig.bannerLink || '#'">
                    <img :src="remoteConfig.bannerImage">
                  </a>
                </div>
            </div>

            <app-popup-profile v-if="type === 'profile' || type === 'profile-edit'" :type="type" @onChange="type = $event" />
            <app-popup-channels v-if="type === 'channels'" />
            <app-popup-favorites v-if="type === 'favorites'" />
            <app-popup-history v-if="type === 'history'" />

        </div>
    </div>
</template>

<script>
import { mapGetters } from "vuex";
import AppPopupProfile from "./AppPopupProfile";
import AppPopupChannels from "./AppPopupChannels";
import AppPopupFavorites from "./AppPopupFavorites";
import AppPopupHistory from "./AppPopupHistory";
export default {
  name: "AppPopup",
  components: {
    AppPopupProfile,
    AppPopupChannels,
    AppPopupFavorites,
    AppPopupHistory
  },
  data() {
    return {
      show: false,
      type: null
    };
  },
  watch: {
    show(value) {
      let className = 'lock-scroll';
      if(value) {
        document.documentElement.classList.add(className);
      } else {
        document.documentElement.classList.remove(className);
      }
    }
  },
  computed: {
    ...mapGetters(["remoteConfig"]),
    bannerStyles() {
      return this.remoteConfig && this.remoteConfig.bannerImage
        ? { "background-image": "url(" + this.remoteConfig.bannerImage + ")" }
        : {};
    }
  },
  methods: {
    onChange(value) {
      this.show = !!value;
      this.type = value;
    }
  },
  beforeMount() {
    this.$eventBus.$on("popup", this.onChange);
  },
  beforeDestroy() {
    this.$eventBus.$off("popup", this.onChange);
  }
};
</script>
