<template>
    <div class="gift">
        <div class="wrapper">
            <span class="block-head">
                Rewards for you
            </span>
            <span class="block-desc">
                We value your initiative. Exchange earned bonus points for exciting rewards!
            </span>
        </div>

        <div class="line-wrap" ref="categoryBar">
            <div class="wrapper" v-if="isInited && sortedGiftList && sortedGiftList.length">
                <div class="coll">

                    <div v-for="(gift, index) in sortedGiftList" :key="index" :ref="'gift_' + gift.id" class="gift-item">
                        <div class="img">
                            <img :src="gift.data.image">
                        </div>
                        <div class="info">
                            <span class="name" @click.prevent="openGift(gift)">{{ gift.name }}</span>
                            <span class="ball">
                                {{ gift.points }} {{ gift.points | pluralize('point,points,points') }}
                            </span>
                        </div>

                        <div class="gift-hover">
                            <div class="center">
                                <span class="name">{{ gift.name }}</span>
                                <span class="desc">{{ gift.data.description }}</span>
                            </div>
                            <a v-if="gift.points <= currentPoints" href="#" class="red-btn" @click.prevent="openGift(gift)">Get for {{ gift.points }} {{ (gift.points) | pluralize('point,points,points') }}</a>
                            <span v-else class="red-btn red-btn-dis" >Not enough  {{ gift.points - currentPoints }} {{ (gift.points - currentPoints) | pluralize('point,points,points') }}</span>
                        </div>
                    </div>

                </div>
            </div>

            <div class="link-wrap">
                <a href="#" class="link">
                    Terms and Conditions
                </a>
            </div>

        </div>

        <div v-if="gift && !success" class="popup">
            <div class="popup-inner popup-inner-gift">
                <a href="#" class="close" @click.prevent="gift = false"></a>
                <div class="info">
                    <span class="name">{{ gift.name }}</span>
                    <span class="desc">{{ gift.data.description }}</span>
                    <a v-if="gift.points <= currentPoints" @click.prevent="getGift(gift)" href="#" class="red-btn">Redeem for {{ gift.points }} {{ gift.points | pluralize('point,points,points') }}</a>
                    <span v-else class="red-btn cancel">Not enough points(</span>
                </div>
                <div class="img">
                    <img :src="gift.data.image">
                </div>
                <div class="bottom">
                    Rewards can take up to 7 business days to be delivered
                </div>
            </div>
        </div>

        <div v-if="gift && success" class="popup">
            <div class="popup-inner popup-inner-gift popup-inner-gift-congr">
                <a href="#" class="close" @click.prevent="success = null;gift = null"></a>
                <div class="info">
                    <span class="name">Congratulations!</span>
                    <span class="desc">
                        Your redemption request can take up to 7 business days to be processed.
For information on the status of your request please contact hr@placeholder.com
                    </span>
                    <a href="#" class="red-btn">Continue</a>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import { mapGetters } from "vuex";
import throttle from "lodash/throttle";
const THROTTLE_SCROLL_TIME = 100;
const THROTTLE_LINE_UPDATE_TIME = 100;
export default {
  name: "AppGifts",
  data() {
    return {
      isMobile: false,
      lineWidth: 0,
      isMounted: false,
      holding: false,
      fixed: false,
      gift: null,
      success: null
    };
  },
  watch: {
      isInited() {
        this.updateLineWidth();
      }
  },
  computed: {
    ...mapGetters(["isInited", "currentUser", "currentPoints", "giftList"]),
    sortedGiftList() {
        let giftList = this.giftList && Object.assign([], this.giftList)
        return giftList && giftList.sort(function(a,b){return a.points - b.points})
    },
  },
  filters: {
    json: value => {
      return JSON.stringify(value, null, 2);
    }
  },
  methods: {
    openGift(gift) {
      this.gift = Object.assign({}, gift);
    },
    updateLineWidth() {
      if (window.innerWidth < 1024) {
          this.isMobile = true;
      } else {
          this.isMobile = false;
      }
      if (!this.sortedGiftList) return
      let enabledGifts = this.sortedGiftList.filter(
        item => item.points <= this.currentPoints
      );
      let width = 0;
      if (!enabledGifts.length) {
        width = 0;
      } else {
        let checkpoint = this.$refs["checkpoint_" + enabledGifts.length];
        if (checkpoint && checkpoint[0]) {
          let rect = checkpoint[0].getBoundingClientRect();
          width = rect.left + rect.width;
        }
      }
      this.lineWidth = width;
    },
    getGift(gift) {
      if (this.holding) return;
      this.holding = true;
      this.$store.dispatch("getGift", gift).then(res => {
        this.success = res;
        this.$store.dispatch("fetchUser");
        this.$store.dispatch("fetchHistory");
        this.$store.dispatch("fetchGifts");
        this.holding = false;
      });
    },
    onScrollTo(id) {
      let element = this.$refs["gift_" + id] || this.$refs["mobile_gift_" + id];      
      if (!element) return;
      let rect = element[0].getBoundingClientRect();
      let headerHeight = document.querySelector(".header").clientHeight;
      let offsetY = rect.top + window.pageYOffset - headerHeight;
      if (offsetY !== 0) {
        window.scrollTo(rect.left, offsetY);
      }
    },
    onScroll() {
      if (!this.$refs["categoryBar"]) return;
      let currentOffset =
        window.pageYOffset || document.documentElement.scrollTop;
      let rect = this.$refs["categoryBar"].getBoundingClientRect();
      let elementOffset = rect.top + window.pageYOffset;
      if (
        currentOffset >= elementOffset &&
        currentOffset < elementOffset + rect.height
      ) {
        this.fixed = true;
      } else {
        this.fixed = false;
      }
    }
  },
  beforeMount() {
    window.addEventListener("resize", throttle(this.updateLineWidth, THROTTLE_LINE_UPDATE_TIME));
    window.addEventListener("scroll", throttle(this.onScroll, THROTTLE_SCROLL_TIME));
  },
  beforeDestroy() {
    window.removeEventListener("resize", throttle(this.updateLineWidth, THROTTLE_LINE_UPDATE_TIME));
    window.removeEventListener("scroll", throttle(this.onScroll, THROTTLE_SCROLL_TIME));
  },
  mounted() {
    this.onScroll();
    this.isMounted = true;
    if (window.innerWidth < 1024) {
        this.isMobile = true;
    }
    this.$eventBus.$on("giftOpen", this.onScrollTo);
  }
};
</script>
