<template>
<div class="right">
    <span class="popup-head">Каналы коммуникации</span>
    <span class="popup-desc">
        Мы ценим вас и благодарим за то, что вы с нами. Поэтому с каждой покупки.
    </span>
    <div class="lk-form">

        <div v-for="(item, index) in items" :key="index" class="lk-row">
            <div class="check-box">
                <input type="checkbox" v-model="item.model" :name="'channel_' + index" :id="'channel_' + index">
                <label :for="'channel_' + index">{{ item.name }}</label>
            </div>
        </div>

        <div class="lk-row">
            <a href="#" class="red-btn edit" @click.prevent="onChange">Сохранить</a>
        </div>

        <div v-if="saved" class="lk-from-success">Все изменения сохранены</div>
    </div>
</div>
</template>

<script>
import { mapGetters } from 'vuex'
const SHOW_MESSAGE_TIME = 2000
const TAGS_UPDATE_TIME = 1000;
export default {
  name: 'AppPopupChannels',
  data () {
    return {
      saved: false,
      timeout: null
    }
  },
  computed: {
    ...mapGetters(['currentUser', 'remoteConfig']),
    items () {
      return (this.remoteConfig && this.remoteConfig.channels || []).map(item => {
        if (item.type === 'system') {
          let value = !!this.currentUser[item.getValue]
          item.origin = value
          item.model = value
        } else if (item.type === 'tag') {
          let value = this.$options.filters['tagExist'](item.value)
          item.origin = value
          item.model = value
        }
        return item
      })
    }
  },
  methods: {
    onUserUpdate (fields) {
      return new Promise((resolve, reject) => {
        let params = {}
        fields.forEach(item => {
          if (Array.isArray(item.setValue)) {
            params[item.setValue[0]] = params[item.setValue[0]] || {}
            params[item.setValue[0]][item.setValue[1]] = item.model ? 1 : 0
          } else {
            params[item.setValue] = item.model
          }
        })
        for (var prop in params) {
          if (params.hasOwnProperty(prop)) {
            params[prop] = JSON.stringify(params[prop])
          }
        }
        if (!Object.keys(params).length) return resolve()
        this.$store.dispatch('userUpdate', params).then(resolve, reject)
      })
    },
    onTagsUpdate (tags) {
      return new Promise((resolve, reject) => {
        let params = {
          add: tags
            .filter(item => item.model && item.model !== item.origin)
            .map(item => item.value),
          remove: tags
            .filter(item => !item.model && item.model !== item.origin)
            .map(item => item.value)
        }
        if (!params.add.length && !params.remove.length) return resolve()
        this.$store.dispatch('changeTags', params).then(resolve, reject)
      })
    },
    onChange (item) {
      if (this.timeout) clearTimeout(this.timeout)
      let systemField = this.items.filter(
        item => item.type === 'system' && item.model !== item.origin
      )
      let tagsField = this.items.filter(
        item => item.type === 'tag' && item.model !== item.origin
      )
      let promises = [
        this.onUserUpdate(systemField),
        this.onTagsUpdate(tagsField)
      ]
      Promise.all(promises).then(() => {
        if (systemField.length) {
          this.saved = true;
          this.timeout = setTimeout(() => {
            this.saved = false;
          }, SHOW_MESSAGE_TIME)
          this.$store.dispatch('fetchUser', true).then(res_user => {
            if (tagsField.length) {
              setTimeout(() => {
                this.$store.dispatch('fetchTags', true).then(res_tags => {
                  this.$store.commit('updateUserInfo', res_user)
                  this.$store.commit('updateTags', res_tags.tags)
                })
              }, TAGS_UPDATE_TIME);
            } else {
              this.$store.commit('updateUserInfo', res_user)
            }
          })
        } else if (tagsField.length) {
          this.saved = true;
          this.timeout = setTimeout(() => {
            this.saved = false;
          }, SHOW_MESSAGE_TIME)
          setTimeout(() => {
            this.$store.dispatch('fetchTags')
          }, TAGS_UPDATE_TIME);
        } else {
          this.saved = false;
        }
      }, () => {
        this.saved = false;
      })

    }
  }
}
</script>
