<template>
  <div>
    <div class="header" :class="{'white': scrolled}">
      <div class="wrapper">
        <a href="#" class="logo" @click.prevent="onClickLogo"></a>
        <div class="header-right">
          <div class="menu">
            <a v-show="!item.hide" v-for="(item, index) in items" :class="{'act': active === item.alias}"
               :key="index" :href="item.href || '#'" @click.prevent="onClick(item)" :target="item.target">{{ item.name }}</a>
          </div>
        </div>
      </div>

    </div>

    <a href="#" class="mob-menu-open" :class="{'close-menu': opened}" @click.prevent="opened = !opened"></a>

    <div class="mob-menu-overlay" :class="{'show': opened}">
      <div class="menu">
        <a v-for="(item, index) in items" :key="index" :class="{'act': active === item.alias}"
          :href="item.href || '#'" @click.prevent="onClick(item)" :target="item.target">{{ item.name }}</a>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import throttle from 'lodash/throttle'

import VueScrollTo from 'vue-scrollto';

let options = {
  easing: 'ease-out',
  offset: -80,
  force: true,
  cancelable: true,
  onStart: function(element) {
    // scrolling started
  },
  onDone: function(element) {
    // scrolling is done
  },
  onCancel: function() {
    // scrolling has been interrupted
  },
  x: false,
  y: true
}

export default {
  name: 'AppHeader',
  props: {
    items: {
      type: Array
    }
  },
  data () {
    return {
      scrolled: false,
      opened: false,
      active: false
    }
  },
  computed: {
    ...mapGetters(['remoteConfig'])
  },
  methods: {
    onClickLogo () {
      window.scrollTo(0, 0)
    },
    onClick (item) {
      this.opened = false
      if (item.ref) {
//        this.$emit('onSelect', item)
        VueScrollTo.scrollTo(item.ref.$el, 400, options)
      } else {
        window.open(item.href, item.target)
      }
      return false
    },
    onScroll () {
      let scrolled = parseInt(window.pageYOffset || document.documentElement.scrollTop)
      if (scrolled > 0) {
        this.scrolled = true
      } else {
        this.scrolled = false
      }
      if (!this.items) return;
      let items = this.items.filter(item => !item.hide)
      if (!items) return
      let scrolledItems = items.filter(item => {
        let rect = item.ref.$el && item.ref.$el.getBoundingClientRect()
        if (!rect) return
        let headerHeight = document.querySelector('.header').clientHeight
//        let lineHeight = document.querySelector('.line').clientHeight
        let offsetY = parseInt(rect.top + window.pageYOffset - headerHeight)
        return offsetY <= scrolled
      })
      if (scrolledItems && scrolledItems.length) {
        this.active = scrolledItems.pop().alias
      }
    }
  },
  beforeMount () {
    this.onScroll()
    window.addEventListener('scroll', throttle(this.onScroll, 100))
  },
  beforeDestroy () {
    window.removeEventListener('scroll', throttle(this.onScroll, 100))
  }
}
</script>
