import Vue from 'vue'
import App from './App.vue'
import store from './store'

import moment from 'moment'
import 'moment/locale/ru'
import MaskedInput from 'vue-text-mask'

import 'isomorphic-fetch'

import './assets/styles/components/normalize.min.css'
import './assets/styles/main.scss'



Vue.component('masked-input', MaskedInput)
Vue.use(require('vue-moment'), {
  moment
})

Vue.config.productionTip = false

Vue.prototype.$eventBus = new Vue()

Vue.filter('tagExist', tag => {
  let tags = store.getters.currentTags || []
  return !!tags.find(item => item.tag === tag)
})

Vue.filter('pluralize', (value, titles) => {
  let cases = [2, 0, 1, 1, 1, 2]
  titles = titles.split(',')
  return titles[(value % 100 > 4 && value % 100 < 20) ? 2 : cases[(value % 10 < 5) ? value % 10 : 5]]
})

Vue.directive('click-outside', {
  bind: function (el, binding, vNode) {
    // Provided expression must evaluate to a function.
    if (typeof binding.value !== 'function') {
      const compName = vNode.context.name
      let warn = `[Vue-click-outside:] provided expression '${binding.expression}' is not a function, but has to be`
      if (compName) {
        warn += `Found in component '${compName}'`
      }

      console.warn(warn)
    }
    // Define Handler and cache it on the element
    const bubble = binding.modifiers.bubble
    const handler = (e) => {
      if (bubble || (!el.contains(e.target) && el !== e.target)) {
        binding.value(e)
      }
    }
    el.__vueClickOutside__ = handler

    // add Event Listeners
    document.addEventListener('click', handler)
  },

  unbind: function (el, binding) {
    // Remove Event Listeners
    document.removeEventListener('click', el.__vueClickOutside__)
    el.__vueClickOutside__ = null
  }
})

new Vue({
  render: h => h(App),
  store,
  mounted: () => {
    Vue.moment().locale('ru')
    store.dispatch('init').then(() => {
      store.dispatch('checkLogin')
    })
  }
}).$mount('#app')
